<template>
  <!--  -->
  <HeaderSearch msg="">
    <template #rightTime>
      <div class="rightSlot">
        <div class="selects">
          <el-select
            v-model="chosenYear"
            placeholder=""
            class="selecter"
            @change="getClassList()"
          >
            <el-option
              v-for="item in academicYear"
              :key="item.dictValue"
              :label="item.dictLabel"
              :value="item.dictValue"
            />
          </el-select>
          <el-select
            v-model="chosenSemester"
            placeholder=""
            class="selecter"
            @change="getClassList()"
          >
            <el-option
              v-for="item in semester"
              :key="item.dictValue"
              :label="item.dictLabel"
              :value="item.dictValue"
            />
          </el-select>
        </div>
      </div>
    </template>
    <template #assignBtn>
      <div class="assignBtn" v-show="showAdd">
        <el-button style="color: #6573c0" text @click="assignDetail()">
          
        </el-button>
      </div>
    </template>
  </HeaderSearch>
  <!--  -->
  <addBtn
    v-show="identity === ''"
    @click="addVisible = true"
  ></addBtn>
  <!--  -->
  <div>
    <el-dialog
      v-model="addVisible"
      title=""
      width="580px"
      :show-close="false"
      :align-center="true"
    >
      <el-form
        :model="classAddForm"
        :rules="rules"
        ref="classAddForm"
        label-position="top"
      >
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="className"
        >
          <el-input v-model="classAddForm.className" autocomplete="off" />
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="instructor"
        >
          <el-autocomplete
            style="width: 540px"
            v-model="classAddForm.instructor"
            :fetch-suggestions="querySearch"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="courseCode"
        >
          <el-input v-model="classAddForm.courseCode" autocomplete="off" />
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="identifier"
        >
          <el-input v-model="classAddForm.identifier" autocomplete="off" />
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="chosenYear"
        >
          <el-select
            v-model="classAddForm.chosenYear"
            placeholder=""
          >
            <el-option
              v-for="item in academicYear"
              :key="item.dictValue"
              :label="item.dictLabel"
              :value="item.dictValue"
            />
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="chosenSemester"
        >
          <el-select
            v-model="classAddForm.chosenSemester"
            placeholder=""
          >
            <el-option
              v-for="item in semester"
              :key="item.dictValue"
              :label="item.dictLabel"
              :value="item.dictValue"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="remark">
          <el-input
            :rows="2"
            type="textarea"
            v-model="classAddForm.remark"
            autocomplete="off"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="goBatchAddClass"></el-button>
          <el-button @click="addVisible = false"></el-button>
          <el-button type="primary" @click="addClass(classAddForm)">
            
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
  <!--  -->
  <div>
    <el-dialog
      v-model="editVisible"
      title=""
      width="580px"
      :show-close="false"
      :align-center="true"
    >
      <el-form
        :model="classEditForm"
        :rules="rules"
        ref="classEditForm"
        label-position="top"
      >
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="className"
        >
          <el-input v-model="classEditForm.className" autocomplete="off" />
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="instructor"
        >
          <el-autocomplete
            style="width: 540px"
            v-model="classEditForm.instructor"
            hide-loading
            :fetch-suggestions="querySearch"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="courseCode"
        >
          <el-input v-model="classEditForm.courseCode" autocomplete="off" />
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="versionName"
        >
          <el-select
            v-model="classEditForm.versionName"
            placeholder=""
          >
            <el-option
              v-for="item in this.detailList"
              :key="item.detailId"
              :label="item.versionName"
              :value="item.detailId"
            />
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="identifier"
        >
          <el-input v-model="classEditForm.identifier" autocomplete="off" />
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="chosenYear"
        >
          <el-select
            v-model="classEditForm.chosenYear"
            placeholder=""
          >
            <el-option
              v-for="item in onlyAcademicYear"
              :key="item.dictValue"
              :label="item.dictLabel"
              :value="item.dictValue"
            />
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="chosenSemester"
        >
          <el-select
            v-model="classEditForm.chosenSemester"
            placeholder=""
          >
            <el-option
              v-for="item in onlySemester"
              :key="item.dictValue"
              :label="item.dictLabel"
              :value="item.dictValue"
            />
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="isRespondent"
        >
          <el-radio-group v-model="classEditForm.isRespondent" class="ml-4">
            <el-radio label="2"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="remark">
          <el-input
            :rows="2"
            type="textarea"
            v-model="classEditForm.remark"
            autocomplete="off"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editVisible = false"></el-button>
          <el-button type="primary" @click="confirmEditClass(classEditForm)">
            
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
  <!--  -->
  <div class="assignDialog">
    <el-dialog
      v-model="isAssign"
      title=""
      width="330px"
      :show-close="false"
      :align-center="true"
    >
      <el-select
        v-model="assignedDetail"
        style="width: 250px; margin-left: 20px"
        placeholder=""
      >
        <el-option
          v-for="(datail, index) in detailList"
          :key="index"
          :label="datail.versionInfo"
          :value="datail.detailId"
        >
        </el-option>
      </el-select>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="isAssign = false"></el-button>
          <el-button type="primary" @click="confirmAssign()">  </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
  <!--  -->
  <el-table
    v-show="hasClass"
    ref="multipleTable"
    class="classesTable"
    :data="classTable"
    @selection-change="handleSelectionChange"
    style="width: 1720px"
    :header-cell-class-name="cellClass"
    :header-cell-style="{
      'padding-left': '20px',
      'font-size': '14.4px',
      height: '48px',
      'font-weight': 'bold',
      color: 'black',
    }"
    :cell-style="{
      'padding-left': '20px',
      'font-size': '16px',
      height: '60px',
    }"
    highlight-current-row
    :row-key="rowKey"
  >
    <el-table-column
      type="selection"
      width="50"
      :selectable="canSelect"
      :reserve-selection="true"
    />
    <el-table-column prop="className" label="" width="210" />
    <el-table-column prop="allTeacherName" label="" width="200" />
    <el-table-column prop="courseCode" label="" width="170" />
    <el-table-column prop="versionName" label="" width="160" />
    <el-table-column prop="identifier" label="" width="270" />
    <el-table-column prop="academicYear" label="" width="150" />
    <el-table-column prop="semester" label="" width="150" />
    <el-table-column prop="remark" label="" width="150" />
    <el-table-column prop="isRespondent" label="" width="60">
      <template #default="scope">
        <div v-if="scope.row.isRespondent == 2">
          <el-tooltip content="" :hide-after="0">
            <el-button link style="color: #3f51b5; cursor: default"
              ><el-icon style="color: #3f51b5"><SetUp /></el-icon>
            </el-button>
          </el-tooltip>
        </div>

        <span v-else></span>
      </template>
    </el-table-column>

    <el-table-column width="150">
      <template #default="scope">
        <el-row>
          <el-col :span="8">
            <el-tooltip content="" :hide-after="0">
              <el-button
                link
                style="color: #3f51b5"
                @click="editClass(scope.$index, scope.row)"
                ><el-icon><Edit /></el-icon
              ></el-button>
            </el-tooltip>
          </el-col>
          <el-col :span="12" v-show="identity === ''">
            <el-tooltip content="" :hide-after="0">
              <el-button
                link
                style="color: #3f51b5"
                @click="deleteClass(scope.$index, scope.row)"
                ><el-icon><Delete /></el-icon
              ></el-button>
            </el-tooltip>
          </el-col>
        </el-row>
      </template>
    </el-table-column>
  </el-table>
  <div class="no-class" v-show="noClass"></div>
  <!--  -->
  <div class="pagination-container" flex>
    <el-row type="flex" justify="center" align="middle" style="margin-top: 8px">
      <el-button v-show="hasClass && showLoadmore" @click="loadMore()"
        ></el-button
      >
    </el-row>
  </div>
</template>

<script>
import HeaderSearch from "@/components/general/headerSearch.vue";
import { getClass, addClass, editClass, setPermission } from "@/api/class";
import { checkTeachers } from "@/api/teacher";
import { getDictionary } from "@/api/dictionary";
import addBtn from "@/components/general/addBtn.vue";
import { Edit, Delete, Plus, SetUp } from "@element-plus/icons-vue";
import { ElMessageBox, ElMessage } from "element-plus";
import { getDetails, assign } from "@/api/basecourse";

export default {
  name: "Class",
  components: {
    HeaderSearch,
    addBtn,
    Edit,
    Delete,
    Plus,
    SetUp,
  },
  data() {
    return {
      hasClass: false,
      noClass: false,
      isRespondent: "0",
      showLoadmore: true,
      isAssign: false,
      showAdd: false,
      detailList: [],
      assignedDetail: "",
      multipleSelection: [],
      selectedCourseId: "",
      canSelectAll: true,
      identity: "",
      searchTeacher: "",
      allTeachers: [],
      classTable: [],
      academicYear: [],
      semester: [],
      onlyAcademicYear: [],
      onlySemester: [],
      chosenYear: "",
      chosenSemester: "",
      currentInfo: {
        departmentId: Number,
        schoolId: Number,
      },
      addVisible: false,
      editVisible: false,
      formLabelWidth: "140px",
      classAddForm: {
        className: "",
        instructor: "",
        courseCode: "",
        identifier: "",
        chosenYear: "",
        chosenSemester: "",
        remark: "",
      },
      classEditForm: {
        className: "",
        instructor: "",
        courseCode: "",
        identifier: "",
        chosenYear: "",
        chosenSemester: "",
        remark: "",
      },
      rules: {
        className: [
          { required: true, message: "", trigger: "blur" },
        ],
        instructor: [
          { required: true, message: "", trigger: "blur" },
        ],
        courseCode: [
          { required: true, message: "", trigger: "blur" },
        ],
        identifier: [
          { required: true, message: "", trigger: "blur" },
        ],
        chosenYear: [
          { required: true, message: "", trigger: "change" },
        ],
        chosenSemester: [
          { required: true, message: "", trigger: "change" },
        ],
      },
      pageNum: 1,
      pageSize: 8,
      total: 0,
    };
  },
  mounted() {
    ///
    this.currentInfo.departmentId = this.$store.state.currentInfo.departmentId;
    this.currentInfo.schoolId = this.$store.state.currentInfo.schoolId;
    this.identity = this.$store.state.currentInfo.identity;
    this.getDictionary();
  },
  computed: {
    currentChange() {
      return this.$store.state.currentInfo;
    },
    teacherAddChange() {
      return this.classAddForm.instructor;
    },
    teacherEditChange() {
      return this.classEditForm.instructor;
    },
    multipleSelectionChange() {
      return this.multipleSelection;
    },
  },
  watch: {
    currentChange: {
      deep: true,
      handler(value) {
        this.currentInfo.departmentId =
          this.$store.state.currentInfo.departmentId;
        this.getClassList();
      },
    },
    teacherAddChange: {
      deep: true,
      handler(value) {
        if (value !== "") {
          this.goSearch(this.classAddForm.instructor);
        } else {
          this.allTeachers = [];
        }
      },
    },
    teacherEditChange: {
      deep: true,
      handler(value) {
        if (value !== "") {
          this.goSearch(this.classEditForm.instructor);
        } else {
          this.allTeachers = [];
        }
      },
    },
    multipleSelectionChange: {
      deep: true,
      handler(value) {
        if (value.length > 0) {
          this.assignedDetail = "";
          this.getDetailList();
          this.showAdd = true;
        } else {
          this.assignedDetail = "";
          this.showAdd = false;
        }
      },
    },
  },
  methods: {
    //
    goBatchAddClass() {
      this.$router.push({ path: "/batchClassAdd" });
    },
    //
    getDictionary() {
      getDictionary().then((res) => {
        console.log("getDictionary", res);
        this.academicYear = res.academic_year;
        this.semester = res.semester;
        //
        this.onlyAcademicYear = JSON.parse(JSON.stringify(res.academic_year));
        this.onlySemester = JSON.parse(JSON.stringify(res.semester));
        //
        let year = {};
        year.dictLabel = "";
        year.dictValue = null;
        this.academicYear.unshift(year);
        let semester = {};
        semester.dictLabel = "";
        semester.dictValue = null;
        this.semester.unshift(semester);
        this.getClassList();
      });
    },
    //
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getClassList();
    },
    //
    getClassList() {
      getClass(
        this.chosenYear,
        this.chosenSemester,
        this.currentInfo.departmentId,
        this.pageSize,
        this.pageNum
      ).then((res) => {
        console.log("getClassList", res);
        if (res.code == 200) {
          this.classTable = res.rows;
          if (this.classTable.length == 0) {
            this.noClass = true;
            this.hasClass = false;
          } else {
            this.hasClass = true;
            this.noClass = false;
          }
          this.total = res.total;
          if (this.pageSize >= res.total) {
            this.showLoadmore = false;
          } else {
            this.showLoadmore = true;
          }
          if (this.classTable.length > 0) {
            for (let i = 0; i < this.classTable.length; i++) {
              this.classTable[i].allTeacherName =
                this.classTable[i].teacherName;
              if (this.classTable[i].additionalTeacherList !== null) {
                this.classTable[i].additionalTeacherList.forEach((teacher) => {
                  this.classTable[i].allTeacherName =
                    this.classTable[i].allTeacherName +
                    "," +
                    teacher.teacherName;
                });
              }

              this.academicYear.forEach((year) => {
                if (year.dictValue == this.classTable[i].academicYear) {
                  this.classTable[i].academicYear = year.dictLabel;
                }
              });
              this.semester.forEach((semester) => {
                if (semester.dictValue == this.classTable[i].semester) {
                  this.classTable[i].semester = semester.dictLabel;
                }
              });
            }
          }
          console.log("#", this.classTable);
        }
      });
    },
    //
    addClass(classAddForm) {
      this.$refs["classAddForm"].validate((valid) => {
        if (valid) {
          addClass(
            this.classAddForm.chosenYear,
            this.classAddForm.chosenSemester,
            this.currentInfo.departmentId,
            this.classAddForm.className,
            this.classAddForm.identifier,
            this.classAddForm.instructor,
            this.classAddForm.courseCode,
            this.classAddForm.remark,
            this.currentInfo.schoolId
          ).then((res) => {
            this.addVisible = false;
            console.log("addClass", res);
          });
        } else {
          // console.log("error submit!!");
          return;
        }
      });
    },
    //
    editClass(index, row) {
      console.log("index", index, "row", row);
      //detailList
      getDetails(
        row.courseId,
        this.currentInfo.departmentId,
        this.currentInfo.schoolId
      ).then((res) => {
        console.log("getDetailList", res);
        this.detailList = res.rows;
      });
      // console.log("@#",this.onlyAcademicYear);
      this.onlyAcademicYear.forEach((year) => {
        if (year.dictLabel == row.academicYear) {
          row.academicYear = year.dictValue;
        }
      });
      this.onlySemester.forEach((semester) => {
        if (semester.dictLabel == row.semester) {
          row.semester = semester.dictValue;
        }
      });
      //
      this.classEditForm.className = row.className;
      this.classEditForm.instructor = row.teacherName;
      this.classEditForm.courseCode = row.courseCode;
      this.classEditForm.identifier = row.identifier;
      this.classEditForm.chosenYear = row.academicYear;
      this.classEditForm.chosenSemester = row.semester;
      this.classEditForm.remark = row.remark;
      this.classEditForm.classId = row.classId;
      this.classEditForm.teacherName = row.teacherName;
      this.classEditForm.teacherNumber = row.teacherNumber;
      this.classEditForm.versionName = row.versionName;
      this.classEditForm.isRespondent = row.isRespondent;
      this.editVisible = true;
      this.onlyAcademicYear.forEach((year) => {
        if (year.dictValue == row.academicYear) {
          row.academicYear = year.dictLabel;
        }
      });
      this.onlySemester.forEach((semester) => {
        if (semester.dictValue == row.semester) {
          row.semester = semester.dictLabel;
        }
      });
    },
    //
    confirmEditClass(classEditForm) {
      classEditForm.academicYear = classEditForm.chosenYear;
      classEditForm.semester = classEditForm.chosenSemester;
      classEditForm.departmentId = this.currentInfo.departmentId;
      classEditForm.schoolId = this.currentInfo.schoolId;
      // this.classEditForm.isRespondent = this.isRespondent
      this.giveRespondentPermission(classEditForm);
      // console.log("", classEditForm);
      editClass(classEditForm).then((res) => {
        console.log("confirmEditClass", res);
        if (res.code == 200) {
          ElMessage({
            type: "success",
            message: "",
            duration: 1000,
          });
          this.editVisible = false;
          this.getClassList();
        }
      });
    },
    //
    deleteClass(index, row) {
      console.log("index", index, "row", row);
      ElMessageBox.confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        this.classTable.pop(row);
      });
    },
    //
    goSearch(searchTeacher) {
      this.allTeachers = [];
      // console.log("@", searchTeacher);
      checkTeachers(
        this.currentInfo.departmentId,
        this.currentInfo.schoolId,
        searchTeacher
      ).then((res) => {
        // console.log("!!", res);
        this.allTeachers = res.data;
      });
    },
    querySearch(queryString, cb) {
      var allTeachers = this.allTeachers;
      if (allTeachers.length > 0) {
        var results = queryString
          ? allTeachers.filter(this.createStateFilter(queryString))
          : allTeachers;
        results = allTeachers.map((item) => ({ value: item }));
        cb(results);
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          cb(results);
        }, 3000 * Math.random());
      }
    },
    //
    createStateFilter(queryString) {
      return (state) => {
        return;
        state.value.toUpperCase().match(queryString.toUpperCase());
      };
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
      // console.log(this.multipleSelection);
      if (this.multipleSelection.length > 0) {
        this.selectedCourseId = this.multipleSelection[0].courseId;
        // console.log("this.selectedCourseId", this.selectedCourseId);
        this.canSelectAll = false;
      } else {
        this.selectedCourseId = "";
        this.canSelectAll = true;
      }
    },
    //disable
    canSelect(row, index) {
      if (this.selectedCourseId == "") {
        return true;
      } else {
        if (row.courseId == this.selectedCourseId) {
          return true;
        } else {
          return false;
        }
      }
    },
    //
    cellClass(row) {
      if (this.canSelectAll && row.columnIndex == 0) {
        return "DisableSelection";
      }
    },
    //detailId
    getDetailList() {
      getDetails(
        this.multipleSelection[0].courseId,
        this.currentInfo.departmentId,
        this.currentInfo.schoolId
      ).then((res) => {
        // console.log("getDetailList", res);
        this.detailList = res.rows;
        this.detailList.forEach((item) => {
          item.versionInfo = item.courseName + "-" + item.versionName;
        });
      });
    },
    //
    assignDetail() {
      // console.log("123", this.multipleSelection);
      this.isAssign = true;
    },
    confirmAssign() {
      console.log("confirmAssign");
      // console.log("@#",this.assignedDetail,this.multipleSelection);
      this.multipleSelection.forEach((item) => {
        item.detailId = this.assignedDetail;
      });
      assign(this.multipleSelection).then((res) => {
        // console.log("assign",res);
        if (res.code == 200) {
          this.isAssign = false;
          ElMessage({
            type: "success",
            message: "",
            duration: 1000,
          });
          this.multipleSelection = [];
          this.$refs.multipleTable.clearSelection();
          this.getClassList();
        } else {
          ElMessage({
            type: "error",
            message: "",
            duration: 1000,
          });
        }
      });
    },
    loadMore() {
      if (this.total - this.pageSize >= 20) {
        this.pageSize += 20;
        this.getClassList();
      } else {
        this.pageSize += this.total - this.pageSize;
        this.getClassList();
      }
    },
    rowKey(row) {
      return row.classId;
    },
    giveRespondentPermission(classEditForm) {
      let msg = {};
      msg.classId = classEditForm.classId;
      msg.departmentId = this.currentInfo.departmentId;
      msg.isRespondent = classEditForm.isRespondent;
      msg.schoolId = this.currentInfo.schoolId;
      let arr = [];
      arr.push(msg);
      setPermission(arr).then((res) => {
        console.log("setPermission", res);
      });
    },
    afv() {
      console.log("123", this.isRespondent);
    },
  },
};
</script>

