import request from '@/utils/request/request'
//
export function getClass(academicYear,semester,departmentId,pageSize,pageNum) {
    return request({
      url: '/classes/list',
      method: 'get',
      params:{
        academicYear:academicYear,
        semester:semester,
        departmentId:departmentId,
        pageSize:pageSize,
        pageNum:pageNum,
      }
    })
  }
  //
export function addClass(academicYear,semester,departmentId,className,identifier,instructor,courseCode,remark,schooldId) {
  return request({
    url: '/classes/add',
    method: 'POST',
    data:{
      academicYear:academicYear,
      semester:semester,
      departmentId:departmentId,
      // 
      className:className,
      // 
      identifier:identifier,
      // 
      instructor:instructor,
      // 
      courseCode:courseCode,
      remark:remark,
      schooldId:schooldId,
    }
  })
}
  //
  export function editClass(array) {
    return request({
      url: '/classes/edit',
      method: 'POST',
      data:array,
    })
}
//
export function setPermission(array) {
  return request({
    url: '/classes/setClassIsRespondent',
    method: 'POST',
    data:array,
  })
}